[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.tabs {
  max-width: 100%;
}

.tabs__controls {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 40px;
}
@media screen and (max-width: 767px) {
  .tabs__controls {
    flex-wrap: wrap;
  }
}

.tabs__control {
  text-transform: uppercase;
  cursor: pointer;
  margin: 0 20px;
  opacity: 0.5;
  min-width: auto;
  justify-content: center;
  width: 25%;
}
@media screen and (max-width: 1023px) {
  .tabs__control {
    margin: 0 10px;
    padding: 15px;
  }
}
@media screen and (max-width: 767px) {
  .tabs__control {
    margin: 10px 2% 0;
    padding: 10px;
    width: 45%;
  }
}
.tabs__control.is-active {
  opacity: 1;
  pointer-events: none;
}

.tabs__content {
  margin-bottom: 40px;
  transition: height 0.3s;
}

.tabs__element {
  padding: 25px;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .tabs__element {
    padding: 15px;
  }
}
.tabs__element p {
  margin: 0;
}
.tabs__element p:not(:last-child) {
  margin: 0 0 20px 0;
}
.tabs__element:nth-child(1) {
  background-color: rgb(250, 235, 215);
}
.tabs__element:nth-child(2) {
  background-color: rgb(229, 250, 215);
}
.tabs__element:nth-child(3) {
  background-color: rgb(180, 188, 233);
}
.tabs__element:nth-child(4) {
  background-color: rgb(255, 187, 204);
}

.tabs--unset .tabs__content {
  height: 100px;
}

.tabs--unset .tabs__element {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

